import React , { PureComponent } from 'react';

class Detail extends PureComponent {
  constructor(props) {
    const { id } = props.router.query;
    this.state = {
      articleId: id
    }
    super(props);
  }
  render() {
    return (
      <div>这里是详情页面文章id为{this.state.articleId}</div>
    )
  };
}

export default Detail;